.Layout {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 12px;
  padding-right: 12px;
  font-family: Arial, Helvetica, sans-serif;
}

.contactEdit{
  width: 250px;
  border: 1px solid;
  display: flex;
  padding: 10px;
}

.contactEdit > p{
  text-align: left;
}

.submit{
  display: block;
  background-color: skyblue;
}


button {
  font-size: 8px;
  margin: 5px;
  border-radius: 5px;
}

.contactList{
  display: flex;
  width: 200px;
  align-items: center;
  justify-content: space-between;
}

.deleteButton{
  color: red  ;
  height: 100%;
}